export default '.el-button{border: 1px solid extraLightSilver;color: baseBlack;}.el-button:hover, .el-button:focus{color: primary;border-color: primary;}.el-button:active{color: buttonActive;border-color: buttonActive;}.el-button.is-disabled, .el-button.is-disabled:hover, .el-button.is-disabled:focus{color: extraLightSilver;background-color: extraLightGray;border-color: baseGray;}.el-button.is-disabled.is-plain, .el-button.is-disabled.is-plain:hover, .el-button.is-disabled.is-plain:focus{border-color: baseGray;color: extraLightSilver;}.el-button.is-active{color: buttonActive;border-color: buttonActive;}.el-button.is-plain:hover, .el-button.is-plain:focus{border-color: primary;color: primary;}.el-button.is-plain:active{border-color: buttonActive;color: buttonActive;}.el-button--primary{color: #fff;background-color: primary;border-color: primary;}.el-button--primary:hover, .el-button--primary:focus{background: buttonHover;border-color: buttonHover;}.el-button--primary:active{background: buttonActive;border-color: buttonActive;}.el-button--primary.is-active{background: buttonActive;border-color: buttonActive;}.el-button--primary.is-plain{border: 1px solid extraLightSilver;color: baseBlack;}.el-button--primary.is-plain:hover, .el-button--primary.is-plain:focus{border-color: primary;color: primary;}.el-button--primary.is-plain:active{border-color: buttonActive;color: buttonActive;}.el-button--text{color: primary;}.el-button--text:hover, .el-button--text:focus{color: buttonHover;}.el-button--text:active{color: buttonActive;}';



